const router = new VueRouter({
  mode: "history",
  routes,
});
router.beforeEach(async (to, from, next) => {
  if (to.meta?.requiresAuth) {
    if (
      auth.currentUser &&
      (await (
        await auth.currentUser.getIdTokenResult()
      ).claims["admin"])
    ) {
      next();
    } else {
      next({
        name: "Sign In",
      });
    }
  } else if (to.meta?.superAdmin) {
    if (
      auth.currentUser &&
      (await (
        await auth.currentUser.getIdTokenResult()
      ).claims["superAdmin"])
    ) {
      next();
    } else if (!auth.currentUser) {
      next({
        name: "Sign In",
      });
    } else {
      Vue.toasted.show("No Super Admin Permission Found. Access Denied", {
        icon: "face",
        type: "error",
      });
    }
  } else {
    next();
  }
});
export default router;
