export class DashBoardCard {
    title: string;
    subTitile: string;
    value: string;
    color: string;
    constructor(title: string,
        subTitile: string,
        value: string,
        color: string) {
        this.title = title;
        this.subTitile = subTitile;
        this.color = color;
        this.value = value;
    }
}