
import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

import Item from '@/components/base/Item.vue';
import ItemGroup from '@/components/base/ItemGroup.vue';
import firebaseAuthService from '@/services/FirebaseAuthService';
import { auth } from '@/firebaseInit';

@Component({
  components: {
    Item,
    ItemGroup
  }
})
export default class NavigationDrawer extends Vue {
  private currUser!: firebase.User | null;
  mounted() {
    this.currUser = auth.currentUser;
  }
  signOut = false
  get drawer() { return this.$store.getters.getDrawer }
  set drawer(value) { this.$store.commit('setDrawer', value) }
  items = [
    {
      icon: 'mdi-view-dashboard',
      title: 'dashboard',
      to: '/',
    },
    {
      icon: 'mdi-account',
      title: 'user',
      to: '/pages/user',
    },
    {
      title: 'rtables',
      icon: 'mdi-clipboard-outline',
      to: '/tables/regular-tables',
    },
    {
      title: 'typography',
      icon: 'mdi-format-font',
      to: '/components/typography',
    },
    {
      title: 'icons',
      icon: 'mdi-chart-bubble',
      to: '/components/icons',
    },
    {
      title: 'google',
      icon: 'mdi-map-marker',
      to: '/maps/google-maps',
    },
    {
      title: 'notifications',
      icon: 'mdi-bell',
      to: '/components/notifications',
    },
  ]

  logOut() {

    firebaseAuthService.signOut(this.$route.path);
    this.signOut = false
    this.currUser= null;
  }
  public data() {
    return {
      barColor: this.$store.state.barColor,
      barImage: this.$store.state.barImage,


      expandOnHover: false,

    }
  }
}