import { firestore, db } from "@/firebaseInit";

export class FirestoreService {
  static async getSingleDocument(
    collection: string,
    key: string,
    converter?: any
  ): Promise<any> {
    try {
      const resp = await firestore
        .collection(collection)
        .doc(key)
        .withConverter(converter)
        .get();
      if (resp.exists) {
        return resp;
      } else {
        throw new Error("Document key does not exists");
      }
    } catch (error) {
      console.error("error occured while fetching document", error);
      throw new Error(error);
    }
  }

  static async getDocument(collection: string, key: string, converter?: any) {
    try {
      const resp = await firestore
        .collection(collection)
        .doc(key)
        .withConverter(converter)
        .get();
      if (resp.exists) {
        return resp;
      } else {
        return null;
      }
    } catch (error) {
      console.error("error occured while fetching document", error);
      throw new Error(error);
    }
  }
  static async getDocuments(
    collection: string,
    converter: any,
    orderby?: string,
    limit = 20
  ) {
    try {
      const resp = await firestore
        .collection(collection)
        .withConverter(converter)
        .limit(limit)
        .get();

      return resp;
    } catch (error) {
      console.error("error occured while fetching document", error);
      throw new Error(error);
    }
  }
  static async getDocumentsByOrder(
    collection: string,
    converter: any,
    orderby: string,
    direction: "desc" | "asc",
    limit = 20
  ) {
    try {
      console.log(collection);
      const resp = await firestore
        .collection(collection)
        .withConverter(converter)
        .orderBy(orderby, direction)
        .limit(limit)
        .get();

      return resp;
    } catch (error) {
      console.error("error occured while fetching document", error);
      throw new Error(error);
    }
  }
  static async getDocumentsByOrderWithStartKey(
    collection: string,
    converter: any,
    orderby: string,
    startAfter: string,
    direction: "desc" | "asc",
    limit = 20
  ) {
    try {
      console.log(collection);
      const resp = await firestore
        .collection(collection)
        .withConverter(converter)
        .orderBy(orderby, direction)
        .startAfter(startAfter)
        .limit(limit)
        .get();

      return resp;
    } catch (error) {
      console.error("error occured while fetching document", error);
      throw new Error(error);
    }
  }
  static async getdocsStartingWithKey(
    collection: string,
    key: string,
    searchKey: string,
    converter: any,
    limit = 5
  ) {
    try {
      const resp = await firestore
        .collection(collection)
        .withConverter(converter)
        .where(searchKey, ">=", key)
        .where(searchKey, "<=", key + "\uf8ff")
        .limit(limit)
        .get();
      return resp;
    } catch (error) {
      console.error("error occured while fetching document", error);
      throw new Error(error);
    }
  }

  static async updateDocument(collection: string, key: string, data: any) {
    try {
      const resp: any = await firestore
        .collection(collection)
        .doc(key)
        .update(data);
      if (resp.exists) {
        return resp.data();
      } else {
        throw new Error("Document key does not exists");
      }
    } catch (error) {
      console.error("error occured while fetching document", error);
      throw new Error(error);
    }
  }
  static async setDocument(
    collection: string,
    key: string,
    data: any,
    converter: any
  ) {
    try {
      const resp: any = await firestore
        .collection(collection)
        .doc(key)
        .withConverter(converter)
        .set(data);
      return resp;
    } catch (error) {
      console.error("error occured while setting document", error);
      throw new Error(error);
    }
  }

  static async addDocumentWithId(
    collection: string,
    key: string,
    data: any,
    converter?: any
  ) {
    try {
      const docsRes = await firestore.collection(collection).doc(key).get();
      if (docsRes.exists) {
        throw new Error("Reocord exists with given id:" + key);
      }
      const resp: any = await firestore
        .collection(collection)
        .doc(key)
        .withConverter(converter)
        .set(data, { merge: true });
      return resp;
    } catch (error) {
      console.error("error occured while setting document", error);
      throw new Error(error);
    }
  }
  static async setOrMergeDocument(
    collection: string,
    key: string,
    data: any,
    converter?: any
  ) {
    try {
      const resp: any = await firestore
        .collection(collection)
        .doc(key)
        .withConverter(converter)
        .set(data, { merge: true });
      return resp;
    } catch (error) {
      console.error("error occured while setting document", error);
      throw new Error(error);
    }
  }

  static async addDocument(collection: string, converter: any, data: any) {
    try {
      const resp: any = await firestore
        .collection(collection)
        .withConverter(converter)
        .add(data);
      return resp;
    } catch (error) {
      console.error("error occured while adding document", error);
      throw new Error(error);
    }
  }
  static async deleteDocument(collection: string, id: string) {
    try {
      const resp = await firestore.collection(collection).doc(id).delete();
    } catch (error) {
      console.error("error occured while fetching document", error);
      throw new Error(error);
    }
  }
  static async updateYearLedger(year: number, value: number, type: string) {
    const cityRef = firestore.collection("analytics").doc(year.toString());
    try {
      const res = await firestore.runTransaction(async (t) => {
        const doc = await t.get(cityRef);
        if (doc.exists) {
          if (type === "Sale") {
            const oldValue = doc.data()?.sale ? doc.data()?.sale : 0;
            const newValue = oldValue + value;
            t.update(cityRef, { sale: newValue });
          } else {
            const oldValue = doc.data()?.expense ? doc.data()?.expense : 0;
            const newValue = oldValue + value;
            t.update(cityRef, { expense: newValue });
          }
        } else {
          if (type === "Sale") {
            t.set(cityRef, { sale: value }, { merge: true });
          } else {
            t.set(cityRef, { expense: value }, { merge: true });
          }
        }
      });
      console.log("Transaction success", res);
    } catch (e) {
      console.log("Transaction failure:", e);
    }
  }
  static async updateMonthLedger(
    year: number,
    month: number,
    value: number,
    type: string
  ) {
    const cityRef = firestore
      .collection("analytics")
      .doc(year.toString())
      .collection("months")
      .doc(month.toString());
    try {
      const res = await firestore.runTransaction(async (t) => {
        const doc = await t.get(cityRef);
        if (doc.exists) {
          if (type === "Sale") {
            const oldValue = doc.data()?.sale ? doc.data()?.sale : 0;
            const newValue = oldValue + value;
            t.update(cityRef, { sale: newValue });
          } else {
            const oldValue = doc.data()?.expense ? doc.data()?.expense : 0;
            const newValue = oldValue + value;
            t.update(cityRef, { expense: newValue });
          }
        } else {
          if (type === "Sale") {
            t.set(cityRef, { sale: value }, { merge: true });
          } else {
            t.set(cityRef, { expense: value }, { merge: true });
          }
        }
      });
      console.log("Transaction success", res);
    } catch (e) {
      console.log("Transaction failure:", e);
    }
  }
  async addKey() {
    console.log("addKey");
  }
}
