import firebase from "firebase/app";

import { auth, firestore } from "@/firebaseInit";
import store from "@/store/index";
import router from "@/router/index";
import FireUser from "@/dto/FireUser";
import { FirestoreService } from "./FirestoreService";
class FirebaseAuthService {
  private fireUser!: FireUser;
  private provider = new firebase.auth.GoogleAuthProvider();
  public async signIn() {
    try {
      const res = await auth.signInWithPopup(this.provider);
      return res;
      // await this.addAuthParams(res.user);

      // store.commit('setSnack', 'sign in successful');

      // router.push("/");
    } catch (err) {
      store.commit("setSnack", err);
    }
  }
  public async addAuthParams(user: firebase.User) {
    try {
      const snapshot = await FirestoreService.getSingleDocument(
        "users",
        user.uid
      );
      const authToken = await user.getIdTokenResult();
      authToken.claims['superAdmin']

      const fireser: FireUser = snapshot.data() as FireUser;
      const isAdmin =
        this.fireUser.admin !== undefined ? this.fireUser.admin : false;
      store.commit("setAdminStatus", isAdmin);
      store.commit("setUserStatus", true);
      store.commit("setUser", this.fireUser);
      console.log(JSON.stringify(this.fireUser));
    } catch (err) {
      store.commit("setSnack", "User Not Found in database");
    }
  }
  public signOut(path: string) {
    auth.signOut().then(() => {
      store.commit("setSnack", "sign out successful");
      store.commit("setUserStatus", false);
      store.commit("setAdminStatus", false);
      store.commit("setUser", null);
      if (path !== "/") router.push("/");
    });
  }
  public async signInWithEmailPassword(email: string, password: string) {
    try {
      const res = await auth.signInWithEmailAndPassword(email, password);
      return res;
    } catch (err) {
      throw new Error(err);
    }
  }
  public async createUserWithEmailAndPassword(email: string, password: string) {
    try {
      const res = await auth.createUserWithEmailAndPassword(email, password);
      return res;
    } catch (err) {
      throw new Error(err);
    }
  }
  public async sendVerificationMail() {
    try {
      const res = await auth.currentUser?.sendEmailVerification();
      return res;
    } catch (err) {
      throw new Error(err);
    }
  }
}
const firebaseAuthService = new FirebaseAuthService();
export default firebaseAuthService;
