import FireUser from '@/dto/FireUser';
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoggedIn: false,
    isAdmin: false,
    snack: '',
    fireUser: null,
    barColor: 'rgba(0, 0, 0, .8), rgba(0, 0, 0, .8)',
    barImage: 'https://demos.creative-tim.com/material-dashboard/assets/img/sidebar-1.jpg',
    drawer: false,
    closeSeller: false,
    addPlatformPanel: false,
    addSellerPanel: false,
    addressPopUpFlag: false,
    shippingAddressPopUpFlag: false,
    invoicePanelFlag: false
  },
  mutations: {
    setUserStatus(state: any, isLoggedIn: boolean) {
      state.isLoggedIn = isLoggedIn;
    },
    setSnack(state: any, snack: boolean) {
      state.snack = snack;
    }
    ,
    setUser(state: any, fireUser: FireUser) {
      state.fireUser = fireUser;
    },
    setAdminStatus(state: any, isAdmin: boolean) {
      state.isAdmin = isAdmin;
    }, setDrawer(state: any, drawer: boolean) {
      state.drawer = drawer;
    }
    , setCloseSeller(state: any, closeSeller: boolean) {
      state.closeSeller = closeSeller;
    }
    , setAddSellerPanel(state: any, addSellerPanel: boolean) {
      state.addSellerPanel = addSellerPanel;
    }, setAddPlatformPanel(state: any, addPlatformPanel: boolean) {
      state.addPlatformPanel = addPlatformPanel;
    }, setAddressPopUpFlag(state: any, addressPopUpFlag: boolean) {
      state.addressPopUpFlag = addressPopUpFlag;
    }, setshippingAddressPopUpFlag(state: any, shippingAddressPopUpFlag: boolean) {
      state.shippingAddressPopUpFlag = shippingAddressPopUpFlag;
    }, setInvoicePanelFlag(state: any, invoicePanelFlag: boolean) {
      state.invoicePanelFlag = invoicePanelFlag;
    }
  },
  getters: {
    getUserStatus(state: any) {
      return state.isLoggedIn;
    },
    getAdminStatus(state: any) {
      return state.isAdmin;
    },
    getSnack(state: any) {
      return state.snack;
    },
    getUser(state: any) {
      return state.fireUser;
    }, getDrawer(state: any) {
      return state.drawer;
    },
    getUserRole(state: any) {
      if (state.fireUser.admin !== null && state.fireUser.admin) {
        return 'Admin';
      } else if (state.fireUser.superAdmin !== null && state.fireUser.superAdmin) {
        return 'Super Admin';
      } else {
        return state.fireUser.email;
      }
    },
    getUserId(state: any) {
      return state.fireUser.firebaseId
    },
    getCloseSeller(state: any) {
      return state.closeSeller
    },
    getAddSellerPanel(state: any) {
      return state.addSellerPanel
    },
    getAddPlatformPanel(state: any) {
      return state.addPlatformPanel
    },
    getAddressPopUpFlag(state: any) {
      return state.addressPopUpFlag;
    },
    getShippingAddressPopUpFlag(state: any) {
      return state.shippingAddressPopUpFlag;
    }, getInvoicePanelFlag(state: any) {
      return state.invoicePanelFlag;
    }

  },
  actions: {
    setAddressPopUpFlag: (context, flag) => {
      context.commit('setAddressPopUpFlag', flag);
    }, setshippingAddressPopUpFlag: (context, flag) => {
      context.commit('setshippingAddressPopUpFlag', flag);
    }, setInvoicePanelFlag: (context, flag) => {
      context.commit('setInvoicePanelFlag', flag);
    }
  },
  modules: {
  }
})
